@use '../_mixins/media-query' as *;


section[class*="bg"]:not(#inner-header){
  padding: var(--s4) 0;
}
div[class*="bg"],figure[class*="bg"] {
  padding: var(--s4);
}

//Orientation
.left {
  margin-left: 0;
  margin-right: auto;
}

.right {
  margin-left: auto;
  margin-right: 0;
}

.center {
  margin-left: auto;
  margin-right: auto;
}

.rel-pos {
  position: relative;
}

.abs-pos {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

